.team {
    padding: 80px 0 100px;
   position: relative;
   // .team__bg 
   &__bg {
      picture {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
      }
      source {
         width: 100%;
         height: 100%;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
    @media (max-width: $md2) {
        padding: 75px 0;
    }
    @media (max-width: $md3) {
        padding: 60px 0;
    }
    // .team__header
    &__header {
        margin-bottom: 57px;
        @media (max-width: 940px) {
            margin-bottom: 45px;
        }
        @media (max-width: $md3) {
            margin-bottom: 35px;
        }
    }
    // .team__title
    &__title {
        &::after {
            content: "";
            background: url('../img/about_line.png') 0 0 no-repeat;
        }
        @media (max-width: $md3) {
            margin-bottom: 20px;
            padding-bottom: 25px;
        }
    }
    // .team__subtitle
    &__subtitle {
        color: #3e3d3d;
        position: relative;
        z-index: 5;
    }
    // .team__body
    &__body {
        display: flex;
        justify-content: center;
        @media (max-width: 940px) {
            flex-wrap: wrap;
        }
    }
    // .team__item
    &__item {
    }
}
.item-team {
    margin: 0 15px;
    position: relative;
   max-width: 270px;
   height: 340px;
   flex: 0 1 33.333%;
    @media (max-width: 940px) {
      flex: 0 1 50%;
      margin: 0 10px 20px 10px;
      &:last-child {
         margin-bottom: 0;
      }
    }
    @media (max-width: 650px) {
       flex: 1 1 100%;
    }
    // .item-team__img
    &__img {
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    // .item-team__hover
    &__hover {
        @media (min-width: $md2) {
            opacity: 0;
            transition: opacity .2s;
            &:hover,
            &:focus {
                opacity: 1;
            }  
        }
    }
}
.hover-item {
    position: absolute;
    bottom: 0;
    left: 0;
    background: -moz-linear-gradient(top, rgba(230,230,230,0) 0%, rgba(230,230,230,0.4) 36%, rgba(230,230,230,0.8) 58%, rgba(230,230,230,1) 100%);
    background: -webkit-linear-gradient(top, rgba(230,230,230,0) 0%,rgba(230,230,230,0.4) 36%,rgba(230,230,230,0.8) 58%,rgba(230,230,230,1) 100%);
    background: linear-gradient(to bottom, rgba(230,230,230,0) 0%,rgba(230,230,230,0.4) 36%,rgba(230,230,230,0.8) 58%,rgba(230,230,230,1) 100%);
    width: 95%;
    height: 95%;
    color: #3e3d3d;
    transform: translate(7px, -9px);
    // .hover-item__body
    &__body {
        position: absolute;
        top: 65%;
        padding-left: 18px;
        width: 146px;
    } 
    // .hover-item__name
    &__name {  
        margin-bottom: 8px;
        a {
            font-weight: 700;
            font-size: 13px;
            letter-spacing: 1.3px;
            color: #3e3d3d;
            @media (min-width: $md2) {
               &:hover,
               &:focus {
                  text-decoration: underline;
               }
            }
        }
    }
    // .hover-item__prof
    &__prof {
        font-family: $crimsonItalic;
        padding-bottom: 10px;
        border-bottom: 2px solid #515151;
        margin-bottom: 18px;
    }
    // .hover-item__social
    &__social {
    }
}
.social-team {
    // .social-team__body
    &__body {
        display: flex;
        margin: 0 -10px;
    }
    // .social-team__item
    &__item {
        border-right: 1px solid #8d8b8b;
        a {
            display: flex;
            align-items: center;
            font-size: 25px;
            color: #8d8b8b;
            padding: 0 10px;
            transition: color .2s;
        }
        &:last-child {
            border: none;
        }
    }
}
