@import url(https://fonts.googleapis.com/css?family=Montserrat:regular,600,700&display=swap);
@import "mixins";
@import "fonts";
@import "nullstyle";
@import "icons";

$fontFamily: "Montserrat";
$crimsonItalic: "CrimsonText-Italic";
$crimsonRoman: "Crimson_Text_Roman";
$amsdam: "Amsdam_Regular";

$minWidth: 320px;
$maxWidth: 1200px;
$maxWidthContainer: 1200px;
$md1: $maxWidthContainer + 10px;
$md2: 991.98px;
$md3: 767.98px;
$md4: 479.98px;

body {
    &._lock {
        overflow: hidden;
    }
}
.container {
    max-width: $maxWidthContainer;
    margin: 0 auto;
    padding: 0 10px;
    width: 100%;
}
.container2 {
    max-width: 1030px;
    margin: 0 auto;
    padding: 0 10px;
    width: 100%;
}
.container3 {
    max-width: 1060px;
    margin: 0 auto;
    padding: 0 10px;
    width: 100%;
}
.wrapper {
    width: 100%;
    min-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    font-family: $fontFamily;
    font-size: 14px;
}
.section-header {
    text-align: center;
}
.title-section {
    font-family: $amsdam;
    font-size: 20px;
    color: #3e3d3d;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding-bottom: 33px;
    position: relative;
    display: inline-flex;
    justify-content: center;
    width: 175px;
    margin-bottom: 40px;
    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 5px;
        left: 0;
    }
}
.subtitle {
    color: #fff;
        font-family: $crimsonRoman;
        color: #cfcfcf;
        letter-spacing: 1.4px;
        line-height: 25px;
        max-width: 1030px;
        margin: 0 auto;
}
.circle {
    position: absolute;
    border: 3px solid #3e3d3d;
    border-radius: 50%;
    width: 72px;
    height: 72px;
    font-size: 13px;
    color: #3e3d3d;
    letter-spacing: 1.3px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    right: -36px;
    @media (max-width: $md3) {
        left: -31px;
        width: 60px;
        height: 60px;
    }
}

.slider-arrow {
   width: 34px;
   height: 34px;
   fill: rgba(255, 255, 255, 0.90);
   transition: .2s;
   @media (min-width: 1200px) {
      &:hover {
         transform: translateZ(8px);
      }
   }
}
.swiper-button-disabled {
   .slider-arrow {
      fill: rgba(255, 255, 255, 0.4);
      cursor: no-drop;
      @media (min-width: 1200px) {
         &:hover {
            transform: translateZ(0);
         }
      }
   }
}
@media (min-width: 1200px) {
   .social-icon:hover,
   .social-icon:focus{
      &.facebook {
         color: #2C72E9;
      }
      &.twitter {
         color: #4EABE0;
      }
      &.behance {
         color: #2252F5;
      }
   }
}



@import "header";
@import "intro";
@import "about";
@import "services";
@import "history";
@import "team";
@import "portfolio";
@import "prices";
@import "blog";
@import "clients";
@import "contact";
@import "swiper";
@import "lightgallery";





