.prices {
    padding: 80px 0 0 0;
    background-color: #e3e3e3;
    @media (max-width: $md2) {
        padding-top: 75px;
    }
    @media (max-width: $md3) {
        padding-top: 60px;
    }
    // .prices__header
    &__header {
        margin-bottom: 80px;
        @media (max-width: $md2) {
            margin-bottom: 50px;
        }
        @media (max-width: 450px) {
            margin-bottom: 35px;
        }
    }
    // .prices__title
    &__title {
        &::after {
            content: "";
            background: url('../img/about_line.png') 0 0 no-repeat;
        }
        @media (max-width: $md2) {
            margin-bottom: 25px;
        }
        @media (max-width: $md3) {
            margin-bottom: 20px;
            padding-bottom: 25px;
        }
    }
    // .prices__subtitle
    &__subtitle {
        color: #3e3d3d;
        @media (max-width: $md3) {
            margin-bottom: 35px;
        }
        @media (max-width: 450px) {
            margin-bottom: 20px;
        }
    }
    // .prices__items
    &__items {
    }
    // .prices__footer
    &__footer {
    }
}
.items-prices {
    // .items-prices__body
    &__body {
        display: flex;
        justify-content: space-between;
        margin: 0 -15px 100px -15px;
        @media (max-width: $md2) {
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 -15px 70px -15px;
        }
        @media (max-width: $md3) {
            margin: 0 -10px 92px -10px;
        }
        @media (max-width: 600px) {
            margin: 0 -10px 50px -10px;
        }
    }
    // .items-prices__item
    &__item {
    }
}
.item-prices {
    padding: 0 15px;
    flex: 0 1 33.333%;
    @media (max-width: $md2) {
        margin-bottom: 30px;
        flex: 0 1 50%;
        &:last-child {
            margin-bottom: 0;
        }
    }
    @media (max-width: $md3) {
        padding: 0 10px;
        margin-bottom: 20px;
    }
    @media (max-width: 600px) {
        flex: 0 1 70%;
    }
    @media (max-width: 500px) {
        flex: 1 1 100%;
    }
    // .item-prices__body{
    &__body {
        background-color: #fff;
        padding: 46px 0 58px 0;
        color: #3e3d3d;
        text-align: center;
        display: flex;
        flex-direction: column;
        height: 100%;
        transition: box-shadow .3s;
        @media (min-width: 1200px) {
           &:hover {
              box-shadow: 0 0 30px rgba(51, 51, 51, 0.4);
              .item-prices__text {
               perspective: 60px;
                 p {
                    &:nth-child(1) {
                        animation: textSize .1s linear forwards;
                    }
                    &:nth-child(2) {
                        animation: textSize .1s linear .1s forwards;
                    }
                    &:nth-child(3) {
                        animation: textSize .1s linear .2s forwards;
                    }
                    &:nth-child(4) {
                        animation: textSize .1s linear .3s forwards;
                    }
                    &:nth-child(5) {
                     animation: textSize .1s linear .4s forwards;
                    }
                 }
              }
              @keyframes textSize {
               0%{
                  transform: translateZ(0);
               }
               50%{
                  transform: translateZ(5px);
               }
               100%{
                  transform: translateZ(0);
               }
            }
           }
        }
        @media (max-width: 600px) {
            padding: 28px 0 40px 0;
        }
    }    
    // .item-prices__title
    &__title {
        font-size: 13px;
        text-transform: uppercase;
        letter-spacing: 1.3px;
        margin-bottom: 27px;
        @media (max-width: 600px) {
            margin-bottom: 15px;
        }
    }
    // .item-prices__icon
    &__icon {
        //padding: 13px 14px;
        border: 2px solid #504f4f;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        margin: 0 auto 24px auto;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            width: 200%;
            height: 2px;
            background-color: #504f4f;
        }
        &::before {
            left: -200%;
        }
        &::after {
            right: -200%;
        }
        @media (max-width: 600px) {
            margin: 0 auto 15px auto;
        }
    }
    // .item-prices__price
    &__price {
        font-size: 70px;
        font-weight: 700;
        position: relative;
        display: inline-block;
        margin: 0 auto 53px auto;
        span {
            position: absolute;
            top: -5px;
            left: -20px;
            width: 16px;
            height: 24px;
            font-size: 28px;
        }
        @media (max-width: $md2) {
            margin: 0 auto 30px auto;
        }
        @media (max-width: 600px) {
            margin: 0 auto 20px auto;
        }
    }
    // .item-prices__text
    &__text {
        font-family: $crimsonItalic;
        font-size: 16px;
        margin-bottom: 50px;
        flex: 1 1 auto;
        p {
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0;
            }
            @media (max-width: 600px) {
                margin-bottom: 15px;
            }
        }
        @media (max-width: $md2) {
            margin-bottom: 40px;
        }
        @media (max-width: 600px) {
            margin-bottom: 25px;
        }
    }
    // .item-prices__btn
    &__btn {
        a {
            border: 2px solid #504f4f;
            width: 134px;
            height: 39px;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            font-size: 13px;
            color: #3e3d3d;
            transition: .2s;
            @media (min-width: $md2) {
                &:hover,
                &:focus {
                    background-color: #504f4f;
                    color: #fff;
                }
            }
        }
    }
}
.footer-prices {
    background-color: #2d2d2f;
    padding: 35px 0;
    @media (min-width: 600px) {
        display: flex;
        align-items: center;
    }
    @media (max-width: 600px) {
        padding: 25px 0 35px 0;
    }
    // .footer-prices__body
    &__body {
        @media (min-width: 600px) {
            display: flex;
            align-items: center;
        }
        @media (max-width: 600px) {
            text-align: center;
        }
    }
    // .footer-prices__column
    &__column{
        &:nth-child(2) {
            max-width: 630px;
            margin-right: 137px;
            @media (max-width: $md1) {
                margin-right: 80px;
            }
            @media (max-width: $md2) {
                margin-right: 30px;
            }
            @media (max-width: 600px) {
                margin: 0 0 20px 0;
            }
        }
    }
    // .footer-prices__icon
    &__icon {
        margin-right: 40px;
        animation: iconRoll 60s linear infinite alternate;
        @media (max-width: 600px) {
            margin: 0 0 20px 0;
        }
        @keyframes iconRoll {
           0%{
              transform: rotate(0);
           }
           100%{
              transform: rotate(360deg);
           }
        }
    }
    // .footer-prices__title
    &__title {
        font-size: 16px;
        color: #d8d8d8;
        letter-spacing: 1.6px;
        margin-bottom: 19px;
        line-height: 18px;
        @media (max-width: $md2) {
            margin-bottom: 14px;
        }
        @media (max-width: 600px) {
            letter-spacing: 1.2px;
        }
    }
    // .footer-prices__text
    &__text {
        font-family: $crimsonItalic;
        font-size: 16px;
        color: #b1afaf;
        line-height: 20px;
    }
    // .footer-prices__btn
    &__btn {
        a {
            width: 134px;
            height: 39px;
            border: 2px solid #fff;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            font-size: 13px;
            color: #e3e3e3;
            letter-spacing: 1.3px;
            transition: .2s;
            @media (min-width: $md2) {
                &:hover,
                &:focus {
                    background-color: #575757;
                    color: #fff;
                }
            }
        }
        @media (max-width: $md2) {
            margin-top: 10px;
        }
        @media (max-width: 600px) {
            margin-top: 0;
        }
    }
}



