.history {
   padding: 80px 0 154px 0;
   background-color: #ededed;
   .container {
      padding: 0 10px 0 13px;
   }
   // .history__header
   @media (max-width: $md2) {
       padding: 75px 0 100px 0;
   }
   @media (max-width: $md3) {
     padding: 60px 0 100px 0;
 }
   &__header {
       margin-bottom: 40px;
       @media (max-width: $md2) {
           margin-bottom: 25px;
       }
       @media (max-width: $md3) {
           margin-bottom: 10px;
       }
   }
    // .history__title
    &__title {
      &::after {
          content: "";
          background: url('../img/about_line.png') 0 0 no-repeat;
      }
      @media (max-width: $md3) {
          margin-bottom: 20px;
          padding-bottom: 25px;
      }
  }
  // .history__subtitle
  &__subtitle {
      color: #3e3d3d;
  }
   // .history__row
   &__row {
      @media (min-width: $md3) {
         display: flex;
      }
      @media (max-width: $md3) {
         padding-left: 39px;
      }
      @media (max-width: 500px) {
         padding-left: 34px;
      }
   }
}
.row-history {
   position: relative;
   margin: 0 -20px;
   padding-top: 72px;
   @media (max-width: $md3) {
      padding-top: 60px;
   }
   @media (max-width: 500px) {
      padding-top: 50px;
   }
   // .row-history__circle
   &__circle {
      position: absolute;
      top: 0;
      left: calc(50% - 36px);
      border: 3px solid #3e3d3d;
      border-radius: 50%;
      width: 72px;
      height: 72px;
      font-size: 13px;
      color: #3e3d3d;
      letter-spacing: 1.3px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 700;
      z-index: 5;
      @media (max-width: $md3) {
         top: 0;
         left: 10px;
         width: 60px;
         height: 60px;
      }
      @media (max-width: 500px) {
         width: 50px;
         height: 50px;
      }
      &_bottom {
         top: 100%;
         background-color: #3e3d3d;
      }
   }
   // .row-history__column
   &__column {
      flex: 0 1 50%;
      border-right: 2px solid #464646;
      @media (max-width: $md3) {
         border-left: 2px solid #464646;
         border-right: none;
      }
      &_left {
         .content-item {
            &::before {
               content: "";
               position: absolute;
               width: 17px;
               height: 17px;
               right: -32px;
               top: 16px;
               background-color: #3e3d3d;
               border-radius: 50%;
               @media (max-width: $md3) {
                  left: -32px;
               }
            }
         }
         .content-item__triangle {
            right: -13px;
            border-right-width: 12px;
            border-left-width: 0;
            border-top: 12px solid #464646;
            border-right-color: transparent;
            border-right-style: solid;
            border-left-color: transparent;
            border-left-style: solid;
            &::after {
               right: -7px;
               border-right-width: 8px;
               border-left-width: 0;
               border-top: 8px solid #ededed;
               border-right-color: transparent;
               border-right-style: solid;
               border-left-color: transparent;
               border-left-style: solid;
            }
            @media (max-width: $md3) {
               right: 100%;
               border-right-width: 0;
               border-left-width: 12px;
               border-top: 12px solid #464646;
               border-right-color: transparent;
               border-right-style: solid;
               border-left-color: transparent;
               border-left-style: solid;
               &::after {
                  left: -7px;
                  border-right-width: 0;
                  border-left-width: 8px;
                  border-top: 8px solid #ededed;
                  border-right-color: transparent;
                  border-right-style: solid;
                  border-left-color: transparent;
                  border-left-style: solid;
               }
            }
         }
      }
      &_right {
         border-right: none;
         padding-top: 50px;
         @media (max-width: $md3) {
            border-left: 2px solid #464646;
            padding-top: 0;
         }
         .content-item {
            &::before {
               content: "";
               position: absolute;
               width: 17px;
               height: 17px;
               left: -32px;
               top: 16px;
               background-color: #3e3d3d;
               border-radius: 50%;
            }
         }
         .content-item__triangle {
            left: -13px;
            border-right-width: 0;
            border-left-width: 12px;
            border-top: 12px solid #464646;
            border-right-color: transparent;
            border-right-style: solid;
            border-left-color: transparent;
            border-left-style: solid;
            &::after {
               left: -7px;
               border-right-width: 0;
               border-left-width: 8px;
               border-top: 8px solid #ededed;
               border-right-color: transparent;
               border-right-style: solid;
               border-left-color: transparent;
               border-left-style: solid;
            }
         }
      }
   }
   // .row-history__item
   &__item {
      padding: 20px;
      position: relative;
   }
   // .row-history__border
   &__border {
      border: 2px solid #464646;
      border-radius: 5px;
   }
   // .row-history__content
   &__content {
   }
}
.content-item {
   padding: 30px;
   position: relative;
   transition: .2s;
   @media (min-width: $md2) {
      &:hover,
      &:focus {
         box-shadow: 0 0 15px rgba(0, 0, 0, 0.692);
          background-color: #3e3d3d;
          .content-item__title,
          .content-item__date,
          .content-item__text {
              color: #e4e4e4;
          }
          .content-item__triangle::after {
              border-top-color: #3e3d3d;
              
          }
      }
  }
  @media (max-width: 550px) {
     padding: 20px;
  }
   // .content-item__title
   &__title {
      display: block;
      font-size: 13px;
      color: #202020;
      letter-spacing: 1.3px;
      text-transform: uppercase;
      margin-bottom: 10px;
      transition: .2s;
      line-height: 15px;
      @media (min-width: 1200px) {
         &:hover {
            text-decoration: underline;
         }
      }
   }
   // .content-item__date
   &__date {
      color: #202020;
        font-size: 11px;
        margin-bottom: 30px;
        transition: .2s;
        @media (max-width: $md3) {
            margin-bottom: 15px;
        }
   }
   // .content-item__text
   &__text {
      font-family: $crimsonRoman;
      letter-spacing: 1.4px;
      line-height: 20px;
      color: #5f5f5f;
      transition: .2s;
      P {
         margin: 0 0 20px;
         &:last-child {
            margin: 0;
         }
      }
   }
   // .content-item__triangle
   &__triangle {
      position: absolute;
      top: 22px;
      &:after {
         content: "";
         top: -10px;
         position: absolute;
         transition: .2s;
      }
   }
}
