.contact {
    // .contact__map
    &__map {
        padding-top: 35%;
        position: relative;
        iframe {
           position: absolute;
           top: 0;
           left: 0;
           width: 100%;
           height: 100%;
        }
        @media (max-width: 1400px) {
            padding-top: 40%;
        }
        @media (max-width: $md3) {
            padding-top: 45%;
        }
        @media (max-width: 600px) {
            padding-top: 55%;
        }
        @media (max-width: $md4) {
            padding-top: 65%;
        }
    }
    // .contact__bg
    &__bg {
       position: relative;
    }
    // .contact__bg-img 
    &__bg-img {
      picture {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
      }
      source {
         width: 100%;
         height: 100%;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
    }
    // .contact__body
    &__body {
        padding: 50px 0 60px 0;
        margin: 0 -15px;
        position: relative;
        @media (min-width: $md3) {
            display: flex;
        }
        @media (max-width: $md3) {
            padding: 50px 0 0 0;
        }
        @media (max-width: $md4) {
            padding: 40px 0 0 0;
        }
        &::after {
            content: "";
            position: absolute;
            width: 97.5%;
            height: 1px;
            bottom: 0;
            left: 15px;
            background-color: #3e3e3e;
        }
    }
    // .contact__column
    &__column {
        padding: 0 15px;
        &:nth-child(1) {
            flex: 0 1 33.5%;
            @media (max-width: $md4) {
                text-align: center;
            }
        }
        &:nth-child(2) {
            padding-top: 70px;
            flex: 0 1 66.5%;
            @media (max-width: $md3) {
                padding-top: 35px;
            }
            @media (max-width: $md4) {
                padding-top: 30px;
            }
        }
    }
    // .contact__info
    &__info {
    }
    // .contact__submit
    &__submit {
    }
}
.info-contact {
    // .info-contact__header
    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 42px;
        @media (max-width: $md3) {
            margin-bottom: 25px;
            justify-content: center;
        }
        @media (max-width: $md4) {
           
            margin-bottom: 20px;
        }
    }
    // .info-contact__icon
    &__icon {
        margin-right: 12px;
    }
    // .info-contact__title
    &__title {
        font-size: 18px;
        color: #fff;
        letter-spacing: 1.8px;
        text-transform: uppercase;
    }
    // .info-contact__text
    &__text {
        font-family: $crimsonRoman;
        color: #949494;
        line-height: 20px;
        letter-spacing: 1.4px;
        margin-bottom: 40px;
        @media (max-width: $md3) {
            margin-bottom: 30px;
        }
    }
    // .info-contact__row
    &__row {
      @media (max-width: $md3) {
         display: flex;
         justify-content: center;
      }
      @media (max-width: 580px) {
         flex-direction: column;
      }
    }
    // .info-contact__item
    &__item {
      @media (max-width: $md3) {
         padding: 0 30px;
         text-align: center;
      }
      @media (max-width: 650px) {
         padding: 0 15px;
      }
    }
    // .info-contact__label
    &__label {
        font-size: 13px;
        color: #e3e3e3;
        letter-spacing: 1.3px;
        text-transform: uppercase;
        margin-bottom: 28px;
        line-height: 20 / 13 * 100%;
        @media (max-width: $md3) {
            margin-bottom: 20px;
        }
    }
    // .info-contact__address
    &__address {
        font-family: $crimsonRoman;
        color: #949494;
        letter-spacing: 1.4px;
        line-height: 20px;
        margin-bottom: 32px;
        @media (max-width: $md3) {
            margin-bottom: 25px;
        }
    }
    // .info-contact__mail
    &__mail {
        font-size: 13px;
        color: #e3e3e3;
        letter-spacing: 1.3px;
        margin-bottom: 15px;
        line-height: 20 / 13 * 100%;
        a {
            font-size: 13px;
            color: #e3e3e3;
            letter-spacing: 1.3px;
            @media (min-width: $md2) {
               &:hover,
               &:focus {
                  text-decoration: underline;
               }
            }
        }
    }
    // .info-contact__phone
    &__phone {
        font-size: 13px;
        color: #e3e3e3;
        letter-spacing: 1.3px;
        margin-bottom: 38px;
        a {
            font-size: 13px;
            color: #e3e3e3;
            letter-spacing: 1.3px;
            @media (min-width: $md2) {
               &:hover,
               &:focus {
                  text-decoration: underline;
               }
            }
        }
        @media (max-width: $md3) {
            margin-bottom: 30px;
        }
    }
    // .info-contact__social
    &__social {
       
    }
}
.social-contact {
    // .social-contact__body
    &__body {
        display: flex;
        margin: 0 -10px;
        @media (max-width: $md3) {
            justify-content: center;
        }
    }
    // .social-contact__link
    &__link {
        position: relative;
        a {
            display: flex;
            align-items: center;
            padding: 0 15px;
            color: #aaa;
            font-size: 25px;
            transition: color .2s;
            @media (min-width: $md2) {
               &:hover {
                  color: rgb(228, 228, 228);
               }
            }
        }
        &:last-child {
            &::after {
                display: none;
            }
        }
        &::after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            height: 100%;
            width: 1px;
            background-color: #aaa;
        }
    }
}
.submit-contact {
    @media (max-width: $md3) {
        margin-top: 30px;
    }
    // .submit-contact__body
    &__body {
        margin: 0 -15px;
        @media (max-width: $md2) {
            margin: 0 -10px;
        }
        @media (max-width: $md3) {
            margin: 0 -5px;
        }
    }
    // .submit-contact__column
    &__column {
        display: flex;
        margin-bottom: 20px;
    }
    // .submit-contact__input
    &__input {
        flex: 0 1 50%;
        padding: 0 15px;
        input {
            width: 100%;
            background-color: transparent;
            border: 2px solid #464646;
            padding: 18px 20px;
            font-family: $fontFamily;
            color: #949494;
            transition: all .2s;
            &::placeholder {
                font-family: $crimsonItalic;
                color: #949494;
                letter-spacing: 1.4px;
            }
            &:focus {
                border: 2px solid #949393;
            }
            &:focus::placeholder {
                opacity: 0;
            }
        }
        @media (max-width: $md2) {
            padding: 0 10px;
        }
        @media (max-width: $md3) {
            padding: 0 5px;
        }
    }
    // .submit-contact__textarea
    &__textarea {
        padding: 0 15px;
        margin-bottom: 22px;
        textarea {
            width: 100%;
            min-height: 97px;
            background-color: transparent;
            resize: none;
            border: 2px solid #464646;
            padding: 15px 20px;
            font-family: $fontFamily;
            color: #949494;
            transition: all .2s;
            &::placeholder {
                font-family: $crimsonItalic;
                color: #949494;
                letter-spacing: 1.4px;
            }
            &:focus {
                border: 2px solid #949393;
            }
            &:focus::placeholder {
                opacity: 0;
            }
        }
        @media (max-width: $md2) {
            padding: 0 10px;
        }
        @media (max-width: $md3) {
            padding: 0 5px;
        }
    }
    // .submit-contact__btn
    &__btn {
        padding: 0 20px;
        button {
            padding: 13px 39px;
            display: inline-block;
            color: #e3e3e3;
            font-size: 13px;
            letter-spacing: 1.3px;
            background-color: #464646;
            transition: .2s;
            &:hover,
            &:focus {
                background-color: #5a5a5a;
            }
        }
        @media (max-width: $md2) {
            padding: 0 15px;
        }
        @media (max-width: $md3) {
           text-align: center;
            padding: 0 5px;
        }
    }
}
.footer {
   position: relative;
   z-index: 1;
    // .footer__body
    &__body {
        padding: 40px 0;
        @media (max-width: $md3) {
            padding: 30px 0;
            text-align: center;
        }
        @media (max-width: $md4) {
            //text-align: center;
        }
    }
    // .footer__copyrigth
    &__copyrigth {
        font-family: $crimsonItalic;
        color: #949494;
        letter-spacing: 1.4px;
    }
}

