.services {
    padding: 80px 0 100px 0;
    position: relative;
    // .services__bg
    &__bg {
      picture {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
      }
      source {
         width: 100%;
         height: 100%;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
    }
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.6);
    }
    @media (max-width: $md2) {
        padding: 75px 0 30px 0;
    }
    @media (max-width: $md3) {
        padding: 60px 0 0 0;
    }
    // .services__body
    &__body {
        position: relative;
        z-index: 1;
    }
    // .services__header
    &__header {
        margin-bottom: 65px;
        @media (max-width: $md3) {
            margin-bottom: 50px;
        }
        @media (max-width: $md4) {
            margin-bottom: 40px;
        }
    }
    // .services__title
    &__title {
        color: #fff;
        &::after {
            content: "";
            background: url('../img/services_line.png') 0 0 no-repeat;
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 5px;
        }
        @media (max-width: $md3) {
            margin-bottom: 30px;
            padding-bottom: 25px;
        }
    }
    // .services__subtitle
    &__subtitle {
    }
    // .services__items
    &__items {
    }
}
.items-services {
    // .items-services__body
    &__body {
        display: flex;
        margin: 0 -15px;
        @media (max-width: $md2) {
            flex-wrap: wrap;
        }
        @media (max-width: $md3) {
            margin: 0 -10px;
        }
    }
    // .items-services__item
    &__item {
        flex: 0 1 25%;
        padding: 0 15px;
        &::before,
        &::after {
            content: "";
            position: absolute;
            width: 35%;
            height: 2px;
            top: 0;
            background-color: #898989;
        }
        &::before {
            left: 15px;
            @media (max-width: $md3) {
                left: 10px;
            }
        }
        &::after {
            right: 15px;
            @media (max-width: $md3) {
                right: 10px;
            }
        }
        @media (max-width: $md2) {
            flex: 0 1 50%;
            width: 50%;
            margin-bottom: 40px;
        }
        @media (max-width: $md3) {
            padding: 0 10px;
        }
        @media (max-width: 550px) {
            flex: 1 1 100%;
        }
    }
}
.item-services {
    position: relative;
    // .item-services__body
    &__body {
        text-align: center;
        padding: 50px 25px 40px 25px;
        border: 2px solid #898989;
        height: 100%;
        border-top: none;
        @media (max-width: 1150px) {
            padding: 50px 20px 40px 20px;
        }
        @media (max-width: 1050px) {
            padding: 50px 15px 40px 15px;
        }
        @media (max-width: $md2) {
            padding: 50px 20px 40px 20px;
        }
        @media (max-width: $md3) {
            padding: 40px 15px 40px 15px;
        }
        @media (max-width: 640px) {
            padding: 40px 10px 40px 10px;
        }
    }
    // .item-services__icon
    &__icon {
        position: absolute;
        top: -10px;
        left: 50%;
        transform: translate(-50%, 0);
        padding: 0 15px;
    }
    // .item-services__title
    &__title {
        font-size: 13px;
        color: #d8d8d8;
        line-height: 18px;
        letter-spacing: 1.3px;
        text-transform: uppercase;
        width: 130px;
        margin: 0 auto 25px auto;
        @media (max-width: $md3) {
            margin: 0 auto 20px auto;
        }
    }
    // .item-services__text
    &__text {
        font-family: $crimsonRoman;
        color: #d8d8d8;
        line-height: 20px;
        letter-spacing: 1.4px;
    }
}
