.about {
    background-color: #e3e3e3;
    padding: 73px 0 45px 0;
    @media (max-width: $md3) {
        padding: 50px 0 0 0;
    }
    @media (max-width: 430px) {
        margin: 0 -8px;
        padding: 50px 0 30px 0;
    }
    // .about__body
    &__body {
        padding-bottom: 50px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 36px;
        @media (max-width: 430px) {
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
    }
    // .about__bg
    &__bg {
        margin-bottom: 60px;
        width: 100%;
        height: 326px;
        position: relative;
        @media (max-width: $md2) {
            margin-bottom: 40px;
        }
        picture {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         }
         source {
            width: 100%;
            height: 100%;
         }
         img {
            width: 100%;
            height: 100%;
            object-fit: cover;
         }
    }
    // .about__title
    &__title {
        &::after {
            content: "";
            background: url('../img/about_line.png') 0 0 no-repeat;
        }
        @media (max-width: $md2) {
            margin-bottom: 25px;
        }
        @media (max-width: 430px) {
            margin-bottom: 20px;
            padding-bottom: 25px;
        }
    }
    // .about__text
    &__text {
        padding: 0 70px;
        margin-bottom: 45px;
        max-width: 100%;
        p {
            font-family: $crimsonRoman;
            font-size: 14px;
            color: #3e3d3d;
            line-height: 25px;
            letter-spacing: 1.4px;
        }
        @media (max-width: $md2) {
            padding: 0 40px;
        }
        @media (max-width: $md3) {
            padding: 0 25px;
            margin-bottom: 35px;
        }
        @media (max-width: 430px) {
            padding: 0 10px;
            margin-bottom: 20px;
        }
    }
    // .about__btn
    &__btn {
    }
    // .about__items
    &__items {
    }
}
.btn-about {
    // .btn-about__body
    &__body {
        display: flex;
    }
    // .btn-about__btn
    &__btn {
        width: 134px;
        padding: 12px 20px;
        border: 2px solid #3e3d3d;
        font-size: 13px;
        color: #3e3d3d;
        letter-spacing: 1.3px;
        margin-right: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .2s;
        &:last-child {
            margin: 0;
        }
        @media (min-width: $md2) {
            &:hover,
            &:focus {
                background-color: #3e3d3d;
                color: #e4e4e4;
            }
        }  
    }
}
.items-about {
    // .items-about__body
    &__body {
        display: flex;
        color: #3e3d3d;
        @media (max-width: $md3) {
            flex-wrap: wrap;
        }
    }
    // .items-about__item
    &__item {
        flex: 1 0 25%;
        text-align: center;
        padding: 3px 0;
        border-right: 1px solid #b0b0b0;
        display: flex;
        flex-direction: column;
        &:last-child {
            border: none;
        }
        @media (max-width: $md3) {
            flex: 1 0 50%;
            margin-bottom: 36px;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                left: 50%;
                transform: translate(-50%, 0);
                bottom: -18px;
                width: 90%;
                height: 1px;
                background-color: #b0b0b0;
            }
            &:nth-child(2) {
                border-right: none;
            }
            &:nth-child(3) {
               &::after {
                   display: none;
                   @media (max-width: 430px) {
                       display: block;
                   }
               }
            }
            &:nth-child(4) {
                &::after {
                    display: none;
                }
            }
        }
        @media (max-width: 430px) {
            flex: 1 0 100%;
            border-right: none;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    // .items-about__icon
    &__icon {
        margin-bottom: 20px;
        @media (max-width: 430px) {
            margin-bottom: 10px;
        }
    }
    // .items-about__title
    &__title {
        width: 90px;
        margin: 0 auto;
        line-height: 18px;
        font-size: 13px;
        letter-spacing: 1.3px;
        text-transform: uppercase;
        margin-bottom: 15px;
        flex: 1 1 auto;
        @media (max-width: 430px) {
            margin-bottom: 10px;
        }
    }
    // .items-about__number
    &__number {
        font-size: 45px;
        font-weight: 700;
    }
}
