.intro {
    display: flex;
    align-items: center;
    position: relative;
    padding: 150px 0 150px;
    text-align: center;
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background-color: rgba(30, 30, 30, 0.4);
    }
    @media (min-height: 730px) {
      height: 100vh;
    }
     // .intro__bg
     &__bg {
      picture {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
      }
      source {
         width: 100%;
         height: 100%;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
    }
    // .intro__body
    &__body {
        text-align: center;
    }
    // .intro__top-title
    &__top-title {
        font-family: $crimsonRoman;
        color: #fff;
        letter-spacing: 1.4px;
        margin-bottom: 25px;
    }
    // .intro__title
    &__title {
        display: inline-block;
        font-size: 35.63px;
        color: #fff;
        font-family: $amsdam;
        letter-spacing: 3.5px;
        padding: 30px 0;
        line-height: 50px;
        position: relative;
        margin-bottom: 45px;
        &::before,
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 5px;
            left: 0;
            background: url('../img/intro-line.png') 0 0 no-repeat;
        }
        &::before {
            top: 0;
        }
        &::after {
            bottom: 0;
        }
    }
    // .intro__btn
    &__btn {
        a {
            display: flex;
            font-size: 15px;
            color: #fff;
            letter-spacing: 1.5px;
            padding: 14px 20px;
            border: 2px solid #fff;
            width: 153px;
            margin: 0 auto;
            position: relative;
            z-index: 3;
            transition: all .2s;
            @media (min-width: 1200px) {
               &:hover,
               &:focus {
                  border-color: rgba(200, 200, 200, 0.9);
               }
            }
        }
    }
    // .intro__slider
    &__slider {
    }
}
.btn-intro {
    position: relative;
    // .btn-intro__link
    &__link {
       transition: background-color .2s;
       @media (min-width: 1200px) {
          &:hover {
             background-color: rgba(200, 200, 200, 0.2);
          }
       }
    }
    // .btn-intro__arrow
    &__arrow {
        width: 16px;
        height: 16px;
        position: absolute;
        bottom: -7px;
        left: calc(50% - 1px);
        border: 2px solid #fff;
        border-radius: 50%;
        transform: translate(-50%, 0);
        z-index: 3;
        background-color: #fff;
        &::before {
            content: "";
            position: absolute;
            width: 2px;
            height: 25px;
            background-color: #fff;
            bottom: -26px;
            left: 5px;
            animation: arrow_line .6s linear infinite alternate;
        }
        &::after {
            content: "";
            position: absolute;
            width: 8px;
            height: 8px;
            bottom: -34px;
            left: 2px;
            border-top: 8px solid #fff;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
            animation: arrow_bottom .6s linear infinite alternate;
        }
    }
}
@keyframes arrow_line {
    0% {
        height: 26px;
        bottom: -27px;
    }
    20% {
        height: 27px;
        bottom: -28px;
    }
    40%{
        height: 28px;
        bottom: -29px;
    }
    60%{
        height: 29px;
        bottom: -30px;
    }
    80%{
        height: 30px;
        bottom: -31px;
    }
    100%{
        height: 31px;
        bottom: -32px;
    }
}
@keyframes arrow_bottom {
    0% {
        bottom: -35px;
    }
    20% {
        bottom: -36px;
    }
    40%{
        bottom: -37px;
    }
    60%{
        bottom: -38px;
    }
    80%{
        bottom: -39px;
    }
    100%{
        bottom: -40px;
    }
}

.mainslider {
    max-width: 525px;
    margin: 0 auto;
    position: relative;
    @media (max-width: $md4) {
        margin-bottom: 45px;
    }
    // .mainslider__body
    &__body {
        width: 525px;
        overflow: hidden;
        position: relative;
        z-index: 3;
        @media (max-width: 560px) {
            width: 300px;
            height: 200px;
            margin: 0 auto 45px auto;
        }
    }
    // .mainslider__slide
    &__slide {
    }
    // .mainslider__arrows
    &__arrows {
    }
    // .mainslider__arrow
    &__arrow {
        top: 45%;
        position: absolute;
        width: 34px;
        height: 34px;
        cursor: pointer;
        z-index: 3;
        display: flex;
        justify-content: center;
        align-items: center;
        perspective: 50px;
        @media (max-width: $md2) {
            top: 68%;
        }
        @media (max-width: 560px) {
            top: 73.5%;
        }
    }
}
.intro-arrow {
    // .intro-arrow_prev
    &_prev {
        left: -60%;
        //background: url('../img/intro-arrow-prev.png') 0 0 no-repeat;
        @media (max-width: 1400px) {
            left: -40%;
        }
        @media (max-width: 1200px) {
         left: -30%;
      }
      @media (max-width: $md2) {
         left: 0;
         top: 103.5%;
      }
      @media (max-width: 600px) {
         left: 20%;
      }
      @media (max-width: 560px) {
         left: 5%;
         top: 120.5%;
      }
    }
    // .intro-arrow_next
    &_next {
        right: -60%;
        //background: url('../img/intro-arrow-next.png') 0 0 no-repeat;
        @media (max-width: 1400px) {
         right: -40%;
      }
      @media (max-width: 1200px) {
         right: -30%;
      }
      @media (max-width: $md2) {
         right: 0;
         top: 103.5%;
      }
      @media (max-width: 600px) {
         right: 20%;
      }
      @media (max-width: 560px) {
         right: 5%;
         top: 120.5%;
      }
    }
}




