.blog {
    padding-top: 80px;
    background-color: #e3e3e3;
    @media (max-width: $md2) {
        padding-top: 75px;
    }
    @media (max-width: $md3) {
        padding-top: 60px;
    }
    // .blog__header
    &__header {
        margin-bottom: 80px;
        @media (max-width: $md2) {
            margin-bottom: 50px;
        }
        @media (max-width: $md3) {
            margin-bottom: 40px;
        }
    }
    // .blog__title
    &__title {
        &::after {
            content: "";
            background: url('../img/about_line.png') 0 0 no-repeat;
        }
        @media (max-width: $md2) {
            margin-bottom: 25px;
        }
        @media (max-width: $md3) {
            margin-bottom: 20px;
            padding-bottom: 25px;
        }
    }
    // .blog__subtitle
    &__subtitle {
        color: #3e3d3d;
        @media (max-width: $md3) {
            margin-bottom: 35px;
        }
        @media (max-width: 450px) {
            margin-bottom: 20px;
        }
    }
    // .blog__body
    &__body {
        
    }
    // .blog__row
    &__row {
        @media (min-width: 1300px) {
            display: flex;
        }
    }
    // .blog__items
    &__items {
    }
}
.item-blog {
    display: flex;
    flex: 0 0 50%;
    min-height: 300px;
    // .item-blog_4
    &_4 {
        .item-blog__description {
            @media (max-width: 600px) {
                order: 2;
            } 
        }
    }
    @media (max-width: 600px) {
        flex-direction: column;
    }
    // .item-blog__img
    &__img {
        flex: 0 0 50%;
        position: relative;
        picture {
           position: absolute;
           top: 0;
           left: 0;
           width: 100%;
           height: 100%;
         }
         source {
            width: 100%;
            height: 100%;
         }
         img {
            width: 100%;
            height: 100%;
            object-fit: cover;
         }
        // .item-blog__img_1
        &_1 {
        }
        // .item-blog__img_2
        &_2 {
            @media (max-width: 1300px) {
                order: 2;
            }
            @media (max-width: 600px) {
                order: 1;
            }
        }
        // .item-blog__img_3
        &_3 {
        }
        // .item-blog__img_4
        &_4 {
        }
        @media (max-width: $md3) {
            padding-top: 50%;
        }
        @media (max-width: 600px) {
            padding-top: 70%;
        }
    }
    // .item-blog__description
    &__description {
        background-color: #2d2d2f;
        padding: 50px 35px 70px 35px;
        flex: 1 1 50%;
        // .item-blog__description_bg
        &_bg {
            @media (max-width: 1300px) {
                order: 1;
            }
            @media (min-width: 1300px) {
                background-color: #555557;
            }
        }
        @media (max-width: $md3) {
            padding: 30px 25px 45px 25px;
        }
    }
    // .item-blog__toptitle
    &__toptitle {
        font-family: $crimsonItalic;
        font-size: 12px;
        color: #e3e3e3;
        max-width: 150px;
        padding-bottom: 10px;
        margin-bottom: 18px;
        border-bottom: 1px solid #454545;
    }
    // .item-blog__title
    &__title {
         display: block;
        font-size: 13px;
        color: #e3e3e3;
        letter-spacing: 1.3px;
        line-height: 18px;
        text-transform: uppercase;
        position: relative;
        padding-left: 12px;
        margin-bottom: 38px;
        @media (min-width: $md2) {
           &:hover,
           &:focus {
              text-decoration: underline;
           }
        }
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
            left: 0;
            width: 4px;
            height: 7px;
            border-left: 4px solid #fff;
            border-top: 3.5px solid transparent;
            border-bottom: 3.5px solid transparent;
        }
        @media (max-width: $md3) {
            margin-bottom: 25px;
        }
    }
    // .item-blog__text
    &__text {
        font-family: $crimsonRoman;
        color: #d8d7d7;
        letter-spacing: 1.4px;
        line-height: 20px;
    }
}
