.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 50;
    background-color: #fff;
    &::before {
        content: "";
        position: fixed;
        width: 100%;
        height: 63px;
        top: 0;
        left: 0;
        z-index: 3;
        background-color: #fff;
        @media (max-width: $md3) {
            height: 50px;
        }
    }
    // .header__body
    &__body {
        height: 63px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md3) {
            height: 50px;
        }
    }
    // .header__label
    &__label {
        display: flex;
        align-items: center;
        z-index: 3;
        @media (max-width: $md2) {
            margin: 0 auto;
            justify-content: center;
        }
    }
    // .header__logo
    &__logo {
        margin-right: 12px;
    }
    // .header__title
    &__title {
        font-size: 18px;
        color: #757575;
        letter-spacing: 1.8px;
        text-transform: uppercase;
        font-weight: 600;
    }
    // .header__social
    &__social {
    }
}
.burger {
   display: none;
   @media (max-width: $md2) {
      display: block;
      position: absolute;
      width: 30px;
      height: 20px;
      top: calc(50% - 10px);
      left: 15px;
      z-index: 5;
      cursor: pointer;
      span {
         position: absolute;
         width: 100%;
         height: 2px;
         background-color: #333;
         top: calc(50% - 1px);
         left: 0;
         transition: transform .2s;
      }
      &::before, &::after {
         content: "";
         position: absolute;
         width: 100%;
         height: 2px;
         background-color: #333;
      }
      &::after {
         bottom: 0;
      }
      &._active {
         span {
            animation: burger_center_line_active .3s forwards ease;
         }
         &::before {
            animation: burger_top_line_active .3s forwards ease;
         }
         &::after {
            animation: burger_bottom_line_active .3s forwards ease;
         }
      }
      &._not-active {
         span {
            animation: burger_center_line_not_active .3s forwards ease;
         }
         &::before {
            animation: burger_top_line_not_active .3s forwards ease;
         }
         &::after {
            animation: burger_bottom_line_not_active .3s forwards ease;
         }
      }
   }
}
@keyframes burger_top_line_active {
   0%{
      top: 0;
      transform: rotate(0);
   }
   50% {
      top: calc(50% - 1px);
      transform: rotate(0);
   }
   100% {
      top: calc(50% - 1px);
      transform: rotate(-45deg);
   }
}
@keyframes burger_top_line_not_active {
   0%{
      top: calc(50% - 1px);
      transform: rotate(-45deg);
   }
   50% {
      top: calc(50% - 1px);
      transform: rotate(0);
   }
   100% {
      top: 0;
      transform: rotate(0);
   }
}
@keyframes burger_bottom_line_active {
   0%{
      bottom: 0;
      transform: rotate(0);
   }
   50% {
      bottom: calc(50% - 1px);
      transform: rotate(0);
   }
   100% {
      bottom: calc(50% - 1px);
      transform: rotate(45deg);
   }
}
@keyframes burger_bottom_line_not_active {
   0%{
      bottom: calc(50% - 1px);
      transform: rotate(45deg);
   }
   50% {
      bottom: calc(50% - 1px);
      transform: rotate(0);
   }
   100% {
      bottom: 0;
      transform: rotate(0);
   }
}
@keyframes burger_center_line_active {
   50%{
      transform: scale(0);
   }
   100% {
      transform: scale(0);
   }
}
@keyframes burger_center_line_not_active {
   0%{
      transform: scale(0);
   }
   50%{
      transform: scale(0);
   }
   100% {
      transform: scale(1);
   }
}

.nav {
    z-index: 3;
    @media (max-width: $md2) {
      position: fixed;
      height: 0;
      width: 100%;
      top: 63px;
      left: 0;
      overflow: auto;
      z-index: 2;
      padding: 0;
      transition: .3s;
      background-color:rgba(51, 51, 51, 0.95);
      @media (max-width: $md3) {
         top: 50px;
      }
      &._active {
         height: 100%;
         padding: 40px 0 120px;
         .nav__list {
               li {
                  a {
                     color: #fff;
                  }
               }
         }
      }
    }
    // .nav__list
    &__list {
       li {
         margin-right: 17px;
         &.active {
            a {
               color: rgb(207, 71, 71);
            }
         }
         &:last-child {
             margin: 0;
         }
         @media (max-width: $md2) {
            margin: 0 0 30px;
            a {
               font-size: 25px;
            }
         }
     }
      @media (min-width: $md2) {
         display: flex;
      }
      @media (max-width: $md2) {
         text-align: center;
      }
    }
   // .nav__link
   &__link {
      font-size: 12px;
      color: rgb(75, 75, 75);
      text-transform: uppercase;
      transition: .2s;
      @media (min-width: $md2) {
         &:hover,
         &:focus
          {
            color: rgb(212, 87, 87);
         }
         &.active {
            color: rgb(207, 71, 71);
         }
      }
   }
}
.social-header {
    z-index: 3;
    @media (max-width: $md2) {
        position: absolute;
        right: 10px;
    }
    @media (max-width: 500px) {
        right: 50%;
        transform: translate(50%, 0);
    }
    // .social-header__body
    &__body {
        display: flex;
    }
    // .social-header__item
    &__item {
        position: relative;
        a {
            display: flex;
            align-items: center;
            color: #aaa;
            font-size: 20px;
            transition: color .2s;
            padding: 0 10px;
            @media (max-width: 500px) {
               font-size: 25px;
               padding: 0 25px;
            }
        }
      &:last-child {
         padding: 0;
         margin: 0;
         &::after {
               display: none;
         }
      }
      &::after {
         content: "";
         position: absolute;
         top: 0;
         right: 0;
         height: 100%;
         width: 1px;
         background-color: #aaa;
      }
    }
}
