.clients {
    padding-top: 80px;
    @media (max-width: $md2) {
        padding-top: 75px;
    }
    @media (max-width: $md3) {
        padding-top: 60px;
    }
    // .clients__header
    &__header {
        margin-bottom: 75px;
        @media (max-width: $md2) {
            margin-bottom: 50px;
        }
        @media (max-width: $md3) {
            margin-bottom: 30px;
        }
    }
    // .clients__title
    &__title {
        &::after {
            content: "";
            background: url('../img/about_line.png') 0 0 no-repeat;
        }
        @media (max-width: $md2) {
            margin-bottom: 25px;
        }
        @media (max-width: $md3) {
            margin-bottom: 20px;
            padding-bottom: 25px;
        }
    }
    // .clients__subtitle
    &__subtitle {
        color: #3e3d3d;
        @media (max-width: $md3) {
            margin-bottom: 35px;
        }
        @media (max-width: 450px) {
            margin-bottom: 20px;
        }
    }
    // .clients__brands
    &__brands {
        margin-bottom: 100px;
        @media (max-width: $md2) {
            margin-bottom: 60px;
        }
        @media (max-width: $md3) {
            margin-bottom: 50px;
        }
    }
    // .clients__slider
    &__slider {
    }
    // .clients__logo
    &__logo {
      text-align: center;
    }
    // .clients__img
    &__img {
    }
    // .clients__reviews
    &__reviews {
    }
}
.reviews-clients {
    text-align: center;
    padding: 75px 0 100px 0;
    position: relative;
    // .reviews-clients__bg
    &__bg {
      picture {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
      }
      source {
         width: 100%;
         height: 100%;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
    }
    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(30, 30, 30, 0.7);
        z-index: 1;
    }
    @media (max-width: $md2) {
        padding: 50px 0 70px;
    }
    @media (max-width: $md4) {
        padding: 35px 0 50px;
    }
    // .reviews-clients__slider
    &__slider {
       max-width: 800px;
       margin: 0 auto;
       overflow: hidden;
       padding: 0 10px;
    }
    // .reviews-clients__body
    &__body {
        margin: 0 auto;
        display: flex;
    }
    // .reviews-clients__item
    &__item {
        height: 100%;
        max-width: 100%;
    }
    // .reviews-clients__icon
    &__icon {
        width: 106px;
        height: 106px;
        margin: 0 auto 40px auto;
        border-radius: 50%;
        overflow: hidden;
        img {
            max-width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    // .reviews-clients__text
    &__text {
        font-family: $crimsonItalic;
        font-size: 20px;
        color: #efefef;
        line-height: 24px;
        margin-bottom: 60px;
        position: relative;
        padding: 0 30px;
        &::before,
        &::after {
            content: "";
            position: absolute;
            width: 27px;
            height: 21px;
        }
        &::before {
            top: -8px;
            left: 0;
            background: url('../img/reviewc_citate1.png');
            @media (max-width: $md3) {
                //left: -18px;
            } 
        }
        &::after {
            right: 0;
            bottom: -5px;
            background: url('../img/reviews_citate2.png');
            @media (max-width: $md3) {
                //right: -18px;
            } 
        }
        @media (max-width: $md2) {
            margin-bottom: 40px;
        }
        @media (max-width: $md3) {
            //padding: 0 10px;
        }
    }
    // .reviews-clients__name
    &__name {
        font-size: 13px;
        color: #e3e3e3;
        letter-spacing: 1.3px;
        line-height: 18px;
        margin-bottom: 15px;
    }
    // .reviews-clients__prof
    &__prof {
        font-family: $crimsonItalic;
        color: #efefef;
        margin-bottom: 40px;
    }
    // .reviews-clients__pagination
    &__pagination {
       width: 100%;
       position: relative;
       display: flex;
       justify-content: center;
       z-index: 2;
      span {
         width: 15px;
         height: 15px;
         background-color: rgba(133, 133, 133, 0.842);
         margin-right: 10px;
         border-radius: 50%;
         cursor: pointer;
         transition: background-color .2s;
         @media (max-width: 500px) {
            width: 18px;
            height: 18px;
            margin-right: 13px;
         }
      }
       .swiper-pagination-bullet-active {
         background-color: #fff;
       }
    }
}


