.portfolio {
    padding-top: 80px;
    @media (max-width: $md2) {
       padding-top: 75px;
    }
    @media (max-width: $md3) {
        padding-top: 60px;
    }
    // .portfolio__top
    &__top {
    }
    // .portfolio__absolute
    &__absolute {
    }
    // .portfolio__header
    &__header {
        margin-bottom: 64px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        @media (max-width: $md3) {
            margin-bottom: 50px;
        }
        @media (max-width: 450px) {
            margin-bottom: 35px;
        }
    }
    // .portfolio__title
    &__title {
        &::after {
            content: "";
            background: url(../img/about_line.png) 0 0 no-repeat;
        }
        @media (max-width: $md3) {
            margin-bottom: 20px;
            padding-bottom: 25px;
        }
    }
    // .portfolio__subtitle
    &__subtitle {
        color: #3e3d3d;
        margin-bottom: 53px;
        @media (max-width: $md3) {
            margin-bottom: 35px;
        }
        @media (max-width: 450px) {
            margin-bottom: 20px;
        }
    }
    // .portfolio__buttons
    &__buttons {
        display: flex;
        text-align: center;
        @media (max-width: 450px) {
            flex-wrap: wrap;
        }
    }
    // .portfolio__btn
    &__btn {
        margin-right: 5px;
        display: flex;
        justify-content: center;
        transition: background-color .2s;
        @media (min-width: 1200px) {
           &:hover,
           &:focus {
            background-color: #cfcfcf;
           }
           
        }
        &.active {
            button {
                color: #fff;
                background-color: #3e3d3d;
            }
        }
        // .portfolio__btn_1
        &_1 {
        
        }
        // .portfolio__btn_2
        &_2 {
           
        }
        // .portfolio__btn_3
        &_3 {

        }
        // .portfolio__btn_4
        &_4 {

        }
        button {
            border: 2px solid #3e3d3d;
            padding: 8.5px 20px;
            font-size: 11px;
            color: #3e3d3d;
            letter-spacing: 1.1px;
            background-color: transparent;
            text-transform: uppercase;
            transition: .2s;
            @media (max-width: 450px) {
                width: 100%;
            }
        }
        &:last-child {
            margin-right: 0;
        }
        @media (max-width: 450px) {
            margin-right: 0;
            padding: 5px;
            flex: 1 1 50%;
        }
    }
    // .portfolio__images
    &__images {
    }
}

.images-portfolio {
    display: none;
    &__img {
      position: relative;
      &::before {
         content: "";
         position: absolute;
         top: 0;
         left: 0;
         right: 0;
         bottom: 0;
         background-color: rgba(0, 0, 0, 0.5);
         z-index: 1;
         opacity: 0;
         transition: .2s;
         cursor: pointer;
      }
      @media (min-width: 1200px) {
         &:hover,
         &:focus {
            .images-portfolio__icon {
               opacity: 1;
            }
            &::before {
               opacity: 1;
            }
         }
      }
      picture {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
      }
      source {
         width: 100%;
         height: 100%;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
    &.active {
        display: block;
    }
    // .images-portfolio__icon
    &__icon {
       position: absolute;
       width: 50px;
       height: 50px;
       top: 50%;
       left: 50%;
       transform: translate(-50%, -50%);
       fill: rgba(255, 255, 255, 0.671);
       transition: .2s;
       opacity: 0;
       z-index: 1;
       cursor: pointer;
    }
    // .images-portfolio_1
    &_1 {
    }
    // .images-portfolio_2
    &_2 {
        &.active {
            .images-portfolio__column {
                @media (min-width: 520px) {
                    flex: 1 1 50%;
                }
            }
            .images-portfolio__column_1 {
                .images-portfolio__img:nth-child(1) {
                    padding-bottom: 60%;
                    @media (max-width: $md2) {
                        padding-bottom: 76%;
                    }
                }
            }
            .images-portfolio__column_2 {
                .images-portfolio__img:nth-child(1) {
                    padding-bottom: 30%;
                    @media (max-width: $md2) {
                        padding-bottom: 38%;
                    }
                }
                .images-portfolio__img:nth-child(2) {
                    padding-bottom: 30%;
                    @media (max-width: $md2) {
                        padding-bottom: 38%;
                    }
                }
            }
        }
    }
    // .images-portfolio_3
    &_3 {
        &.active {
            .images-portfolio__column {
                @media (min-width: 520px) {
                    flex: 1 1 50%;
                }
            }
            .images-portfolio__column_1 {
                .images-portfolio__img:nth-child(1) {
                    padding-bottom: 30%;
                    @media (max-width: $md2) {
                        padding-bottom: 38%;
                    }
                }
                .images-portfolio__img:nth-child(2) {
                    padding-bottom: 30%;
                    @media (max-width: $md2) {
                        padding-bottom: 38%;
                    }
                }
            }
            .images-portfolio__column_2 {
                .images-portfolio__img:nth-child(1) {
                    padding-bottom: 60%;
                    @media (max-width: $md2) {
                        padding-bottom: 76%;
                    }
                }
            }
        }
    }
    // .images-portfolio_4
    &_4 {
        &.active {
            .images-portfolio__column {
                @media (min-width: 520px) {
                    flex: 1 1 50%;
                }
            }
            .images-portfolio__column_1 {
                .images-portfolio__img:nth-child(1) {
                    padding-bottom: 60%;
                    @media (max-width: $md2) {
                        padding-bottom: 76%;
                    }
                }
            }
            .images-portfolio__column_2 {
                .images-portfolio__img:nth-child(1) {
                    padding-bottom: 60%;
                    @media (max-width: $md2) {
                        padding-bottom: 76%;
                    }
                }
            }
        }
    }
    // .images-portfolio__body
    &__body {
        display: flex;
        transition: .6s;
        @media (max-width: $md2) {
            flex-wrap: wrap;
        }
    }
    // .images-portfolio__column
    &__column {
        flex: 0 1 25%;
        @media (max-width: $md2) {
            flex: 1 1 50%;
        }
        @media (max-width: 520px) {
            flex: 1 1 100%;
        }
        // .images-portfolio__column_1
        &_1 {
            .images-portfolio__img:nth-child(1) {
                padding-bottom: 90.5%;
            }
            .images-portfolio__img:nth-child(2) {
                padding-bottom: 57.5%;
            }
        }
        // .images-portfolio__column_2
        &_2 {
            .images-portfolio__img:nth-child(1) {
                padding-bottom: 62.5%;
            }
            .images-portfolio__img:nth-child(2) {
                padding-bottom: 85.5%;
            }
        }
        // .images-portfolio__column_3
        &_3 {
            .images-portfolio__img:nth-child(1) {
                padding-bottom: 43.5%;
            }
            .images-portfolio__img:nth-child(2) {
                padding-bottom: 104.5%;
            }
        }
        // .images-portfolio__column_4
        &_4 {
            .images-portfolio__img:nth-child(1) {
                padding-bottom: 94.5%;
            }
            .images-portfolio__img:nth-child(2) {
                padding-bottom: 53.5%;
            }
        }
    }
    // .images-portfolio__img
    &__img {
    }
}


